<template>
  <div>
    <LayoutBox one="区级账号">
      <el-form size="small" label-width="80px" inline>
        <el-form-item>
          <router-link to="/admin_district_user_add"><el-button type="primary">添加区级管理账号</el-button></router-link>
        </el-form-item>
        <el-form-item label="账号">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="地区">
          <el-input></el-input>
        </el-form-item>
      </el-form>
      <el-table :data="areaUserLsit" size="small">
        <el-table-column prop="user.loginName" label="账号" align="center"></el-table-column>
        <el-table-column prop="user.name" label="昵称" align="center"></el-table-column>
        <el-table-column label="地区" align="center">
          <template slot-scope="scope">
            <div class="area_group">
              <span v-for="(item, i) in scope.row.list" :key="i">{{ item.area }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="onEdit(scope)">编辑</el-button>
            <el-button type="danger" size="mini" @click="onLift(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInfo.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInfo.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'admin_district_user',

  data () {
    return {
      queryInfo: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      areaUserLsit: [], // 区级管理员列表
    }
  },

  components: {
    LayoutBox
  },

  methods: {
    // 点击编辑
    onEdit (scope) {
      this.$router.push({ path: '/admin_district_user_add', query: { userId: scope.row.userId }})
    },
    // 点击解除
    onLift (scope) {
      this.$confirm(`此操作将解除【${scope.row.user.name}】的区级管理资格, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => { this.apiAreauserDelete(scope) })
    },
    // 选择每页显示多少条
    handleSizeChange (pageSize) {
      this.queryInfo.pageSize = pageSize
    },
    // 分页
    handleCurrentChange (pageNo) {
      this.queryInfo.pageNo = pageNo
    },

    // api--------------------------------
    // 分页查询
    apiAreauserQuery () {
      let data = {
        ...this.queryInfo
      }
      this.$http.post('/woho/areauser/query', data)
        .then((res) => {
          this.areaUserLsit = res.data.data.list
          this.queryInfo.total = res.data.data.total
        })
    },
    // 删除区级代理
    apiAreauserDelete (scope) {
      this.$http.delete(`/woho/areauser/deleteByUserId/${scope.row.userId}`)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('解除关系成功')
            this.areaUserLsit.splice(scope.$index, 1)
          } else { this.$message.error(res.data.msg) }
        })
    },
  },

  created () {
    this.apiAreauserQuery()
  }
}
</script>

<style lang="scss" scoped>
  .area_group{
    display: flex;
    flex-direction: column;
  }
</style>
